import { memo } from 'react';
import dynamic from 'next/dynamic';
import { I_Scene } from '@containers/Home/types';
import { ThemeCompactWrapper } from './styled';
import { useSelector } from 'react-redux';
import { selectIsMatterportProject } from '@containers/Home/selectors';

const Header = dynamic(() => import('./Header'));
const Tour = dynamic(() => import('@containers/Home/components/Tour'), {
  ssr: false,
});
const ListScene = dynamic(() => import('./ListScene'));
const Controlbar = dynamic(() => import('./Controlbar'), { ssr: false });
const PostScene = dynamic(() => import('./PostScene'));

interface Props {
  onNextScene: () => void;
  onChangeScene: (scene: I_Scene) => void;
}

export const ThemeCompact = memo(({ onChangeScene, onNextScene }: Props) => {
  const isMatterportProject = useSelector(selectIsMatterportProject);

  return (
    <ThemeCompactWrapper className={isMatterportProject ? 'matterport' : ''}>
      <Header />
      <ListScene onChangeScene={onChangeScene} />
      <Tour onChangeScene={onChangeScene} onNextScene={onNextScene}>
        {({ krpanoRef }) => <Controlbar krpanoRef={krpanoRef} />}
      </Tour>
      <PostScene />
    </ThemeCompactWrapper>
  );
});

export default ThemeCompact;
